import * as THREE from "three";
import * as dat from "dat.gui";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { ShaderPass } from "three/examples/jsm/postprocessing/ShaderPass";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import Camera from "@/Camera";
import Resources from "@/Resources";
import World from "@/world";
import  BlurPass from "@/passes/BlurPass";
import  GlowsPass  from "@/passes/GlowsPass";
import { Sizes, Time } from "@/utils";
export default class Application {
  constructor(_options) {
    this.$canvas = _options.$canvas;

    this.time = new Time();
    this.sizes = new Sizes();
    this.resources = new Resources();

    this.setConfig();
    this.setRenderer();
    this.setCamera();
    this.setPasses();
    this.setWorld();
  }

  setConfig() {
    this.config = {};
    this.config.debug = window.location.hash === '#debug'
    this.config.cyberTruck = window.location.hash === '#cybertruck'
    this.config.touch = false;
    window.addEventListener(
      "touchstart",
      () => {
        this.config.touch = true;
        this.world.controls.setTouch();

        this.passes.horizontalBlurPass.strength = 1;
        this.passes.horizontalBlurPass.material.uniforms.uStrength.value =
          new THREE.Vector2(this.passes.horizontalBlurPass.strength, 0);
        this.passes.verticalBlurPass.strength = 1;
        this.passes.verticalBlurPass.material.uniforms.uStrength.value =
          new THREE.Vector2(0, this.passes.verticalBlurPass.strength);
      },
      { once: true }
    );
  }
  setRenderer() {
    this.scene = new THREE.Scene();

    this.renderer = new THREE.WebGLRenderer({
      canvas: this.$canvas,
      alpha: true,
    });

    this.renderer.setClearColor(0x000000, 1);
    this.renderer.setPixelRatio(2);
    this.renderer.setSize(
      this.sizes.viewport.width,
      this.sizes.viewport.height
    );
    this.renderer.physicallyCorrectLights = true;
    this.renderer.gammaFactor = 2.2;
    this.renderer.gammaOutPut = true;
    this.renderer.autoClear = false;

    this.sizes.on("resize", () => {
      this.renderer.setSize(
        this.sizes.viewport.width,
        this.sizes.viewport.height
      );
    });
  }

  setCamera() {
    this.camera = new Camera({
      sizes: this.sizes,
      renderer: this.renderer,
      time: this.time,
      config: this.config,
    });
    this.scene.add(this.camera.container);

    this.time.on('tick', () =>
    {
        if(this.world && this.world.car)
        {
            this.camera.target.x = this.world.car.chassis.object.position.x
            this.camera.target.y = this.world.car.chassis.object.position.y
        }
    })
  }

  setPasses() {
    this.passes = {
      composer: new EffectComposer(this.renderer),
      renderPass: new RenderPass(this.scene, this.camera.instance),
      horizontalBlurPass: new ShaderPass(BlurPass),
      verticalBlurPass: new ShaderPass(BlurPass),
      glowsPass: new ShaderPass(GlowsPass),
    };
    this.passes.horizontalBlurPass.strength = this.config.touch ? 0 : 1;
    this.passes.horizontalBlurPass.material.uniforms = {
      uStrength: {
        value: new THREE.Vector2(this.passes.horizontalBlurPass.strength, 0),
      },
      uResolution: {
        value: new THREE.Vector2(
          this.sizes.viewport.width,
          this.sizes.viewport.height
        ),
      },
    };

    this.passes.verticalBlurPass.strength = this.config.touch ? 0 : 1;
    this.passes.verticalBlurPass.material.uniforms = {
      uStrength: {
        value: new THREE.Vector2(0, this.passes.verticalBlurPass.strength),
      },
      uResolution: {
        value: new THREE.Vector2(
          this.sizes.viewport.width,
          this.sizes.viewport.height
        ),
      },
    };

    this.passes.glowsPass.color = "#ffcfe0";
    this.passes.glowsPass.material.uniforms = {
      uPosition: { value: new THREE.Vector2(0, 0.25) },
      uRadius: { value: 0.7 },
      uColor: { value: new THREE.Color(this.passes.glowsPass.color) },
      uAlpha: { value: 0.55 },
    };

    this.passes.composer.addPass(this.passes.renderPass);
    this.passes.composer.addPass(this.passes.horizontalBlurPass);
    this.passes.composer.addPass(this.passes.verticalBlurPass);
    this.passes.composer.addPass(this.passes.glowsPass);

    this.time.on("tick", () => {
      this.passes.horizontalBlurPass.enabled =
        this.passes.horizontalBlurPass.material.uniforms.uStrength.value.x > 0;
      this.passes.verticalBlurPass.enabled =
        this.passes.verticalBlurPass.material.uniforms.uStrength.value.y > 0;
      // Renderer
      this.passes.composer.render();
      // this.renderer.domElement.style.background = 'black'
      // this.renderer.render(this.scene, this.camera.instance)
    });

    this.sizes.on("resize", () => {
      this.renderer.setSize(
        this.sizes.viewport.width,
        this.sizes.viewport.height
      );
      this.passes.composer.setSize(
        this.sizes.viewport.width,
        this.sizes.viewport.height
      );
      this.passes.horizontalBlurPass.material.uniforms = {
        ...this.passes.horizontalBlurPass.material.uniforms,
        uResolution: {
          value: {
            x: this.sizes.viewport.width,
            y: this.sizes.viewport.height,
          },
        },
      };
      this.passes.verticalBlurPass.material.uniforms = {
        ...this.passes.verticalBlurPass.material.uniforms,
        uResolution: {
          value: {
            x: this.sizes.viewport.width,
            y: this.sizes.viewport.height,
          },
        },
      };
    });
  }
  setWorld() {
    this.world = new World({
      sizes: this.sizes,
      renderer: this.renderer,
      time: this.time,
      resources: this.resources,
      camera: this.camera,
      config: this.config,
      passes: this.passes
    });
    this.scene.add(this.world.container);
  }
}
