import { EventEmitter } from "./index";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { FBXLoader } from "three/examples/jsm/loaders/FBXLoader";
import {  DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader";

// 
export default class Loader extends EventEmitter {
  constructor() {
    // 
    super();

    // 
    this.setLoaders();
    // 
    this.toLoad = 0;
    // 
    this.loaded = 0;
    // 
    this.items = {};
  }

  // 
  setLoaders() {
    // 
    this.loaders = [];

    // 
    this.loaders.push({
      extensions: ["jpg", "png"],
      action: (index, _resource) => {
        const image = new Image();
        image.addEventListener("load", () => {
          this.fileLoadEnd(index,_resource, image);
        });
        image.addEventListener("error", () => {
          this.fileLoadEnd(index,_resource, image);
        });
        image.src = _resource.source;
      },
    });

    // DRACO
    const dracoLoader = new DRACOLoader();
    dracoLoader.setDecoderPath('draco/');
    dracoLoader.setDecoderConfig({ type: "js" });
    this.loaders.push({
      extensions: ["drc"],
      action: (index, _resource) => {
        dracoLoader.load(_resource.source, (_data) => {
          this.fileLoadEnd(index,_resource, _data);
          DRACOLoader.releaseDecoderModule();
        });
      },
    });

    // GLTF
    const gltfLoader = new GLTFLoader();
    gltfLoader.setDRACOLoader(dracoLoader);
    this.loaders.push({
      extensions: ["glb", "gltf"],
      action: (index, _resource) => {
        gltfLoader.load(_resource.source, (_data) => {
          this.fileLoadEnd(index,_resource, _data);
        });
      },
    });

    // FBX
    const fbxLoader = new FBXLoader();
    this.loaders.push({
      extensions: ["fbx"],
      action: (index, _resource) => {
        fbxLoader.load(_resource.source, (_data) => {
          this.fileLoadEnd(index,_resource, _data);
        });
      },
    });
  }

  // 
  load(_resources = []) {
    // 
    for (let i=0; i<_resources.length; i++ ) {
      const _resource = _resources[i]
      // 1
      this.toLoad++;
      // 
      const extensionMatch = _resource.source.match(/\.([a-z]+)$/);
      if (typeof extensionMatch[1] !== "undefined") {
        const extension = extensionMatch[1];
        // 
        const loader = this.loaders.find((_loader) =>
          _loader.extensions.find((_extension) => _extension === extension)
        );
        // 
        if (loader) {
          loader.action(i, _resource);
        } else {
          console.warn(`Cannot found loader for ${_resource}`);
        }
      } else {
        console.warn(`Cannot found extension of ${_resource}`);
      }
    }
  }

  // 
  fileLoadEnd(index, _resource, _data) {
    // 
    this.loaded++;
    // 
    this.items[_resource.name] = _data;
    // fileEnd
    this.trigger("fileEnd", [_resource, _data]);
    if (this.loaded === this.toLoad) {
      // end
      this.trigger("end");
    }
  }
}
